export default {
	data() {
		return {
			reasons: [
				"不讲（公共）卫生",
				"大声喧哗吵闹，影响他人休息",
				"不讲秩序，霸占公共区域",
				"浪费水、电、气等公共资源",
				"态度蛮横，难以沟通",
				"经常带人回家，影响正常居住",
				"其他"
			],
			text: '',
			textLength: 0,
			maxlength:50,
			checkedList: []
		}
	},
	methods: {
		onLoad(options) {
			var id = options.id;
		},
		back() {
			uni.navigateTo({
				url: './friendDetail',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		onNavigationBarButtonTap() {
			uni.showToast({
				title:'提交成功'
			})
			console.log(this.checkedList);
			console.log(this.text);
			setTimeout(function(){	
				uni.navigateBack({
				 delta: 1
			    })},1500)		
		 },
		choose(i) {
			//判断选项是否被选中，即选中的数组里是否存在改选项
			if (this.checkedList.includes(i)) {
				this.checkedList = this.checkedList.filter(function(ele) {
					return ele != i;
				});
			} else {
				this.checkedList.push(i);
			}
		}
	},
	watch: {
		text(value) {
			this.textLength = value.length;
		}
	}
}
